// ============= FOOTER ================

footer {
  background: #282828;
  color: beige;
  flex: 1 0 auto;

  & > ul {
    list-style: none;
    padding: 0;
    margin: 0;

    @media screen and (max-width: 399px) {
      margin-bottom: 15px;
    }
    @media screen and (min-width: 400px) {
      display: flex;
      justify-content: space-evenly;
    }
  }
}

footer a {
  color: beige;
}

footer > ul > li {
  display: block;
}

footer > ul > li > a {
  display: block;
  font-size: 21px;
  padding: 10px 20px;
}

.spacer {
  flex: 2 1 auto;
}
